import { defineStore, acceptHMRUpdate } from 'pinia'
import { menuList } from '../router/unshiftMenuData'
interface State {
    userData?: object
    token?: string
}
const useUserStore = defineStore('user', <object>{
    state: (): State => ({
        userData: {},
        token: '123',
    }),
    getters: {
        getUser(state: any) {
            return state.userData
        },
        getToken(state: any) {
            return state.token
        },
    },
    actions: {
        //解决报红this 指向问题
        setStoreThis(): any {
            const _this = this
            return _this
        },
        //更新用户信息
        updateState(userItem: any) {
            this.setStoreThis().userData = {
                ...userItem,
            }
            localStorage.setItem(
                'userData',
                JSON.stringify(this.setStoreThis().userData)
            )
        },
        //保存token
        setToken(token: string) {
            // const _this: any = this
            this.setStoreThis().token = token
            localStorage.setItem('token', this.setStoreThis().token)
        },
        //用户登录
        userLogin() {
            //模拟获取用户登录信息 token
            const token = '123123'
            this.setToken(token)
        },
        //获取用户信息
        async getUserInfo() {
            return new Promise((resolve, reject) => {
                const userData = {
                    token: '123123123',
                    name: '张三',
                    id: '1',
                    //权限信息
                    permission: {
                        //菜单列表
                        menus: menuList,

                        //按钮权限---后续需要
                        points: ['login'],
                    },
                }
                this.updateState(userData)
                resolve(userData)
            })
        },
        //用户登出
        outLogin() {
            this.updateState({})
            //回到登录页面 ----  暂时没有登录页 先用首页代替
            // router.replace('/login')
        },
    },
    // 所有数据持久化
    // persist: true,
    // 持久化存储插件其他配置
    persist: {
        // 修改存储中使用的键名称，默认为当前 Store的 id
        key: 'user',
        // 修改为 sessionStorage，默认为 localStorage
        storage: sessionStorage,
        // 部分持久化状态的点符号路径数组，[]意味着没有状态被持久化(默认为undefined，持久化整个状态)
        // paths: [],
    },
})
// Pinia 是 vuex 新替代方案。Pinia 中热更新实现，借助 import.meta
if (import.meta.hot) {
    import.meta.hot.accept(acceptHMRUpdate(useUserStore, import.meta.hot))
}
export default useUserStore
