import { createApp } from 'vue'
import './style.css'
import App from './App.vue'
import router from './router'
import ElementPlus from 'element-plus' //引入组件    npm i  element-plus
import 'element-plus/dist/index.css' //引入组件样式
// import * as Icons from '@element-plus/icons-vue' //注意这个包需要单独引入 npm install @element-plus/icons-vue
// import { toLine } from '@/utils'
import { createPinia } from 'pinia'
import domDirective from '@/utils/domDirective'
import locale from 'element-plus/es/locale/lang/zh-cn'
import showMessage from '@/utils/showModal'
import VueI18n from '@/utils/i18n'
import 'animate.css/animate.min.css' //引入动画
import bxIcon from '@/components/boxIcons/bxIcon.vue'
// import VueDemi from 'vue-demi'
import './permission'
const app = createApp(App)
const pinia = createPinia()
//注意 全局注册会牺牲一点 性能
// 使用时 需要el-icon方式 进行编写
// for (let i in Icons) {
//     //注册全局组件
//     app.component(`el-icon-${toLine(i)}`, (Icons as any)[i])
// }
app.component('bx-icon', bxIcon)
app.directive('domDirective', domDirective) //按钮权限
app.use(router)
    .use(ElementPlus, { locale })
    .use(pinia)
    .use(showMessage)
    .use(VueI18n)
// .use(phUi)
app.mount('#app')
