import { defineStore, acceptHMRUpdate } from 'pinia'
import router from '../router/index'
import { filterRouters } from '@/utils/route'
import { unshiftMenu, whiteNav } from '../router/unshiftMenuData'
interface CounterState {
    path?: any[]
    navList?: any[]
}
//去重 路由中 相同path的数据剔除
const uniqArr = (array: any) => {
    const uniqueArray = array.filter((obj: any, index: any, arr: any) => {
        return arr.findIndex((o: any) => o.path === obj.path) === index
    })
    return uniqueArray
}
//从本地获取路由信息
const path = JSON.parse(sessionStorage.getItem('path') as any)
const useUserStore = defineStore('routes', <object>{
    state: (): CounterState => ({
        //路由信息
        path:
            path && path.length
                ? path
                : [
                      {
                          icon: 'Sort',
                          path: '/home',
                          title: '首页',
                      },
                  ],
        //左侧菜单信息
        navList: [],
    }),
    getters: {},
    actions: {
        //解决报红this 指向问题
        setStoreThis(): any {
            const _this = this
            return _this
        },
        updatePath(route: any) {
            this.setStoreThis().path.push(route)
            this.setStoreThis().path = uniqArr(this.setStoreThis().path)
            sessionStorage.setItem(
                'path',
                JSON.stringify(this.setStoreThis().path)
            )
        },
        //删除path
        deletePath(value: any) {
            //删除过滤
            this.setStoreThis().path = this.setStoreThis().path?.filter(
                (item: any, idx: number) => {
                    return item.path !== value.path
                }
            )
            // console.log(this.path, 'ppppp')
            //重新存储path
            sessionStorage.setItem(
                'path',
                JSON.stringify(this.setStoreThis().path)
            )
        },
        //获取用户信息后 拿到菜单
        setNavListData(data: any) {
            this.setStoreThis().navList = []
            let filterRoutes = filterRouters(router.getRoutes())
            console.log(filterRoutes, 'filterRoutes')
            //过滤出非权限菜单的静态路由
            filterRoutes = filterRoutes.filter((item: any) => {
                return !whiteNav.includes(item.path)
            })
            this.setStoreThis().navList = filterRoutes.map((item: any) => {
                return {
                    icon: item.meta.icon,
                    title: item.meta.title,
                    path: item.path,
                    children:
                        item.children && item.children.length
                            ? item.children.map((itx: any) => {
                                  return {
                                      icon: itx.meta.icon,
                                      title: itx.meta.title,
                                      path: itx.path,
                                  }
                              })
                            : [],
                }
            })
            // console.log(this.navList, 'navList')
            unshiftMenu.forEach((item) => {
                this.setStoreThis().navList.unshift(item)
            })

            // console.log(this.navList, '菜单信息')
        },
    },
    // 所有数据持久化
    // persist: true,
    // 持久化存储插件其他配置
    persist: {
        // 修改存储中使用的键名称，默认为当前 Store的 id
        key: 'routes',
        // 修改为 sessionStorage，默认为 localStorage
        storage: sessionStorage,
        // 部分持久化状态的点符号路径数组，[]意味着没有状态被持久化(默认为undefined，持久化整个状态)
        // paths: [],
    },
})
// Pinia 是 vuex 新替代方案。Pinia 中热更新实现，借助 import.meta
if (import.meta.hot) {
    import.meta.hot.accept(acceptHMRUpdate(useUserStore, import.meta.hot))
}
export default useUserStore
