import { createApp } from 'vue'
import ModalBox from '../views/modal.vue'
interface Props {
    message: string
    ok: Function
    cancel: Function
}
const showMessage = ({ message, ok, cancel }: Props) => {
    const app = createApp(ModalBox, {
        message,
        onOk() {
            ok && ok()
            app.unmount()
            div.remove()
        },
        onCancel() {
            cancel && cancel()
            app.unmount()
            div.remove()
        },
    })
    const div = document.createElement('div')
    document.body.appendChild(div)
    app.mount(div)
}
showMessage.install = (app: any) => {
    // 注册全局属性，类似于 Vue2 的 Vue.prototype
    app.config.globalProperties.$ShowMessage = ({
        message,
        ok,
        cancel,
    }: Props) => showMessage({ message, ok, cancel })
}
export default showMessage
