import path from 'path'

// 判断是不是空值
function isNull(data: any) {
    if (!data) return true
    if (JSON.stringify(data) === '{}') return true
    if (JSON.stringify(data) === '[]') return true
}

const getChildrenRoutes = (routes: any) => {
    const result: any[] = []
    // 将带有children的路由单独处理
    routes.forEach((item: any) => {
        // console.log(item,'itemmmmmmmmm')
        if (item.children && item.children.length > 0) {
            result.push(...item.children)
        }
    })
    return result
}
// 处理重复且脱离层级的路由
export const filterRouters = (routes: any) => {
    // 获取所有的子集路由
    const childrenRoutes = getChildrenRoutes(routes)
    // console.log(childrenRoutes, 'childrenRoutes')
    // 根据子集路由进行查重的操作
    return routes.filter((route: any) => {
        return !childrenRoutes.find((childrenRoute) => {
            // 重复的剔除
            return childrenRoute.path === route.path
        })
    })
}

// 根据routes 数据 返回想要的对应的menu 的数据
export function generateMenus(routes: any, basePath = '') {
    // console.log(path.resolve('123'))
    const result: any[] = []
    // 遍历路由表
    routes.forEach((item: any) => {
        // 不存在 children && 不存在 meta 直接 return
        if (isNull(item.meta) && isNull(item.children)) return
        // 存在 children 不存在 meta，进入迭代
        if (isNull(item.meta) && !isNull(item.children)) {
            result.push(...generateMenus(item.children))
            return
        }
        // 合并 path 作为跳转路径
        const routePath = path.resolve(basePath, item.path)
        // console.log(routePath, 'routePath')
        // console.log(result, 'result')
        // 路由分离之后，存在同名父路由的情况，需要单独处理
        let route = result.find((item) => item.path === routePath)
        // console.log(route, 'route')
        if (!route) {
            route = {
                ...item,
                path: routePath,
                children: [],
            }
            // icon 与 title 必须全部存在
            if (route.meta.icon && route.meta.title) {
                // meta 存在生成 route 对象，放入 arr
                result.push(route)
            }
        }

        // 存在 children 进入迭代到children
        if (item.children) {
            route.children.push(...generateMenus(item.children, route.path))
        }
    })
    return result
}
