<template>
    <div class="header">
        <span @click="toggle">
            <!-- <el-icon-expand style="margin-right: 0.625rem" ></el-icon-expand> -->
            <bx-icon class="bx-right-indent" v-if="isCollapse"
                style="margin-right: 0.625rem;color: #409eff;font-size: 1.7rem;cursor: pointer;"></bx-icon>
            <bx-icon class="bx-left-indent" v-else style="color: #409eff;font-size: 1.7rem;cursor: pointer;"> </bx-icon>
            <!-- <el-icon-fold v-else></el-icon-fold> -->
        </span>
        <div>
            <el-button @click="breadClick(item)" v-for="(item, i) in path " :key="i"
                :type="item.path === activePath ? 'primary' : void 0" size="small">{{ item.title }}
                <bx-icon class="bx-x" @click.stop="closeRoute(item)"
                    :class="{ 'white-color': item.path === activePath }" style="margin-left: 0.4375rem;"></bx-icon>
                <!-- <el-icon-close @click.stop="closeRoute(item)" :class="{ 'white-color': item.path === activePath }"
                    style="width: 0.875rem;height: 0.875rem;margin-left: 0.4375rem;" v-if="i !== 0"></el-icon-close> -->
            </el-button>
        </div>
    </div>
</template>
<script lang="ts">
export default {
    name: 'navHeader',
}
</script>
<script lang="ts" setup>
import { ref, defineEmits, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import useStore from '../../../../store/index'
import { storeToRefs } from 'pinia'
const { routes } = useStore()
const { path } = storeToRefs(routes)
const router = useRouter()

console.log(path, 'sssss')
type Props = {
    isCollapse: boolean
}
const route = useRoute()
const props = withDefaults(defineProps<Props>(), {
    isCollapse: () => true,
})
const emits = defineEmits(['update:isCollapse'])
/**
 * @desc 控制侧边栏的展开收缩
 */
const toggle = () => {
    emits('update:isCollapse', !props.isCollapse)
}
/**
 * @desc 面包屑点击跳转路由
 * 
 */
const breadClick = (item: any) => {
    console.log(item)
    router.push({
        path: item.path
    })
}
//右键点击
const closeRoute = (item: any) => {
    const index = routes.$state.path.findIndex(r => {
        return item.path === r.path
    })
    console.log(index)
    if (index === 0) return
    //回到路径的上一级
    router.replace(routes.$state.path[index - 1].path)
    routes.deletePath(item)
}
//当前路由路径
const activePath = ref('')
//监听路由
watch(
    () => router.currentRoute.value,
    (newValue: any) => {
        console.log('newValue', newValue)
        activePath.value = newValue.path
    },
    { immediate: true }
)
</script>
<style lang="scss" scoped>
.header {
    width: 100%;
    height: 60px;
    padding-left: 20px;
    display: flex;
    // align-items: center;
    flex-direction: column;
    justify-content: flex-end;

    span {
        height: 1.5em;
    }

    svg {
        width: 1.5em !important;
        height: 1.5em;
        cursor: pointer;
        color: #409eff;
    }
}

:deep(.el-button) {
    border-bottom: none;
}

.white-color {
    color: #fff !important;
}
</style>
