import { createI18n } from 'vue-i18n'

const messages = {
    zh: {
        message: {
            user: '用户信息',
            language: '语言切换',
        },
    },
    en: {
        message: {
            user: 'UserInfo',
            language: 'language',
        },
    },
}

const VueI18n = createI18n({
    legacy: false,
    locale: 'zh', // 默认语言
    messages,
})

export default VueI18n
