            <div class="box">{{ message }}</div>
            <div style="display: flex;margin-top: 1.25rem;width: 100%;justify-content: space-around;">
                <ElButton @click="cancel">取消</ElButton>
                <ElButton @click="ok" type="primary">确认</ElButton>
            </div>
        </div>
    </div>
</template>
<script lang='ts' setup>
import { ElButton } from 'element-plus'
import { ref } from 'vue'
type Props = {
    message: string
}
const props = withDefaults(defineProps<Props>(), {
    message: '',
})
const emits = defineEmits(['ok', 'cancel'])
const ok = () => {
    console.log('点击了ok')
    emits('ok')
}
const cancel = () => {
    console.log('点击了关闭')
    emits('cancel')
}
</script>
