<template>
    <!-- el-menu  最外层 -->
    <!-- el-sub-menu 如果存在二级菜单的标签 -->
    <!-- el-menu-item 只有一级菜单的标签 -->
    <!-- v-bind="$attrs"  继承来自父组件的所有属性 -->
    <el-menu :defaultActive="defaultActive" :router="router" v-bind="$attrs" :default-openeds="[$route.path]">
        <template v-for="(item, i) in data " :key="i">
            <!-- 只渲染一级菜单 -->
            <el-menu-item v-if="!item[children] || !item[children].length" :index="item[index]"
                @click="clickMenuItem(item, void 0)">
                <!-- 图标 -->
                <!-- <component v-if="item[icon]" :is="`el-icon-${toLine(item[icon] as string)}`"></component> -->
                <bx-icon :bx-class='`${item[icon]}`' v-if="item[icon]" style="margin-right: 0.5rem" />
                <!-- 导航名称 -->
                <span class="hover-text">{{ item[name] }}</span>
            </el-menu-item>
            <!-- 二级菜单渲染 -->
            <el-sub-menu v-if="item[children] && item[children].length" :index="item[index]">
                <template #title>
                    <!-- <component v-if="item[icon]" :is="`el-icon-${toLine(item[icon] as string)}`"></component> -->
                    <bx-icon :bx-class='`${item[icon]}`' v-if="item[icon]" style="margin-right: 0.5rem" />
                    <!-- 导航名称 -->
                    <span class="hover-text">{{ item[name] }}</span>
                </template>
                <el-menu-item v-for="(item1, index1) in item[children]" :key="index1" :index="item1[index]"
                    @click="clickMenuItem(item[children], index1)">
                    <!-- 图标 -->
                    <!-- <component v-if="item1[icon]" :is="`el-icon-${toLine(item1[icon] as string)}`"></component> -->
                    <bx-icon :bx-class='`${item1[icon]}`' v-if="item1[icon]" style="margin-right: 0.5rem" />
                    <!-- 导航名称 -->
                    <span class="hover-text">{{ item1[name] }}</span>
                </el-menu-item>
            </el-sub-menu>
        </template>
    </el-menu>
</template>
<script lang='ts' setup>
import { ref, PropType } from 'vue'
import { toLine } from '@/utils'
import { storeToRefs } from 'pinia';
import useStore from '../../../store/index'
const { routes } = useStore();
const { path } = storeToRefs(routes);
const props = defineProps({
    //导航的数据
    data: {
        type: Array as PropType<any[]>,
        required: true
    },
    //默认选中的菜单  传入当前菜单的index
    defaultActive: {
        type: String,
        default: ''
    },
    //是否是路由模式
    router: {
        type: Boolean,
        default: false
    },
    //自定义键名
    //菜单标题的键名
    name: {
        type: String,
        default: 'title'
    },
    //菜单标识的键名
    index: {
        type: String,
        default: 'index'
    },
    //图标的键名
    icon: {
        type: String,
        default: 'icon'
    },
    //子菜单的键名
    children: {
        type: String,
        default: 'children'
    }
})
//菜单点击存入路由数据
const clickMenuItem = (event: any, index: any) => {
    console.log(event, 'event')
    if (Array.isArray(event) && event.length) {
        routes.updatePath(event[index])
    } else {
        routes.updatePath(event)
    }
    console.log(routes.$state)
}
// console.log(props.data)
</script>
<style lang='scss' scoped>
svg {
    margin-right: 5px;
    width: 1em;
    height: 1em;
}

//一级菜单hover字体颜色
:deep(.el-sub-menu__title):hover {
    color: #fff !important;
}

//二级菜单hover 字体颜色
:deep(.el-menu-item):hover {
    color: #fff !important;
}

//二级菜单展开色背景色
//:deep(.el-sub-menu .el-menu-item) {
// background-color: #A8ABB2;
//}

:deep(.el-sub-menu__title) {
    // color: #fff !important;
    padding: 0;
    padding-left: 20px;
}
</style>