import useStore from './store/index'
import { storeToRefs } from 'pinia'
import router from './router'
import Container from './components/container/src/index.vue'
const modules = import.meta.glob('./views/**/*.vue')

// 路由白名单  没有权限可以进入的路由
const whiteList = ['/login', '/404']
// 1 处理路由前置守卫
router.beforeEach(async (to, from, next) => {
    const { user, routes } = useStore()
    const { userData, token } = storeToRefs(user)
    // console.log(user.$state, '111')
    // console.log(user.getUser.name, 'user.getUser.name')

    // 1如果用户已经登录 则不允许进入 login
    // 2用户未登录则只允许进入login
    // -- 用户已经登录 token存在
    if (token) {
        // debugger
        console.log(1)
        if (to.path === '/') {
            // return
            //如果正常去首页 则放行
            next('/home')
        } else {
            //用户登陆了 但是没有去首页或者登录页
            // 获取用户的菜单权限列表--进行判断
            //先判断有没有用户信息

            //用户信息不存在
            if (!user.getUser.name) {
                // return
                try {
                    //获取用户信息
                    const data = await user.getUserInfo()
                    console.log(data, 'usersssssssssssss')
                    const { menus } = data.permission
                    if (!menus.length) return
                    //菜单追加

                    // 动态路由追加
                    menus.forEach((item: any) => {
                        // console.log(item,'item11111111111')
                        router.addRoute({
                            path: item.path,
                            component: Container,
                            redirect: item.redirect,
                            meta: {
                                ...item.meta,
                            },
                            children:
                                item.children && item.children.length
                                    ? item.children.map((itxRoute: any) => {
                                          return {
                                              path: itxRoute.path,
                                              component:
                                                  modules[
                                                      `./views/${itxRoute.component}.vue`
                                                  ],
                                              meta: itxRoute.meta,
                                          }
                                      })
                                    : [],
                        })
                    })
                    console.log('====================================')
                    console.log(router.getRoutes())
                    console.log('====================================')
                    routes.setNavListData(menus)
                    if (to.path === '/404' && to.redirectedFrom !== undefined) {
                        next({
                            path: to.redirectedFrom.fullPath,
                            replace: true,
                        })
                    } else {
                        next({ ...to, replace: true }) // 跳转以后动态路由才可生效
                    }
                } catch (err) {
                    // resetRouter()
                    // next('/login')
                }
            } else {
                next()
            }
        }
    } else {
        // 用户未登录 去的是白名单页面直接放行
        if (whiteList.indexOf(to.path) > -1) {
            next()
        } else {
            next('/login')
            // nProgress.done()
        }
    }
})

